// main page elements
body {
	font-size: 14px;
	// color: $dark
	// font-family: '', sans-serif
	font-weight: 400;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.full-page--vertical {
	.slide {
		min-height: 100vh;
		max-height: 100vh;
		position: relative;
		img {
			position: absolute; }
		.flex {
			position: absolute;
			left: 100px;
			top: 33%;
			z-index: 2; } } }

.slide__text {
	h2 {
		font-size: 100px;
		color: $white;
		margin-bottom: 30px; }
	p {
		color: $white;
		margin-bottom: 30px;
		max-width: 500px; }
	a {
		max-width: 200px;
		width: 100%;
		height: 50px;
		background: $gray;
		display: flex;
		align-items: center;
		justify-content: center;
		color: $white;
		font-size: 18px;
		border-radius: 10px;
		svg {
			margin-left: 50px;
			path {
				fill: $white; } } } }

.slick-dots--dop {
	position: absolute;
	left: 6px;
	bottom: 12%;
	transform: rotate(90deg);
	span {
		margin-right: 5px;
		font-size: 12px;
		color: $white; } }
.slick-dots {
	gap: 35px;
	justify-content: center;
	display: flex;
	flex-direction: column;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 20px;
	&:before {
		position: absolute;
		z-index: 1;
		left: 10px;
		content: '';
		height: 65%;
		width: 2px;
		background: $gray; }
	li {
		left: 8.5px;
		position: relative;
		z-index: 2;
		width: 5px;
		height: 5px;
		border-radius: 100%;
		background: $gray;
		cursor: pointer;
		button {
			font-size: 0px;
			cursor: pointer; } }
	.slick-active {
		left: 0;
		width: 22px;
		height: 22px;
		cursor: unset;
		button {
			cursor: unset;
			width: 22px;
			height: 22px;
			border-radius: 100%;
			background: inherit;
			font-size: 10px;
			color: white;
			display: flex;
			align-items: center;
			justify-content: center; } } }
